.side-menu-wrapper {
	position: absolute;
	left: 0;
	right: 85vw;
	display: grid;
	background: lightgray;
	> div:nth-child(2) {
		border-top: 2px solid rgba($color: #000000, $alpha: .3);
		border-bottom: 2px solid rgba($color: #000000, $alpha: .3);
	}
	& .menu-item {
		padding: 1rem 1.5rem;
		display: grid;
		grid-auto-flow: column;
		column-gap: .5rem;
		cursor: pointer;
		transition: background-color 0.3s linear;
		color: $main-blue;
		&:hover {
			background-color: rgba($color: $main-blue, $alpha: .2);
		}
	}
	> .menu-footer {
		padding: 1rem .5rem 3rem .5rem;
		display: grid;
		text-align: center;
	}
}

