@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
body,html{font-family:'Lato', sans-serif;margin:0}.dashboard-wrapper{margin-top:60px;margin-left:14vw;max-width:85vw;box-sizing:border-box;overflow:hidden}.header-wrapper{display:grid;grid-template-columns:60px 1fr .8fr .4fr;height:60px;background-color:#5f6cb0;color:white;position:fixed;top:0;left:0;right:0;z-index:3}.header-wrapper>.logo{display:grid;align-content:center}.header-wrapper>.logo>h4{margin:0;color:#fff}.header-wrapper>.menu-toggler{font-size:32px;display:grid;place-content:center}.header-wrapper>.search{display:grid;align-content:center}.header-wrapper>.search>div{display:grid;grid-template-columns:32px 1fr;background-color:rgba(255,255,255,0.7);border-radius:.3rem}.header-wrapper>.search>div>*:not(input){place-self:center}.header-wrapper>.search>div>input{border:none;outline:none;background:transparent;height:32px}.header-wrapper>.options{display:grid;align-content:center;grid-auto-flow:column;place-items:center;height:32px;align-self:center}.header-wrapper>.options>.avatar{width:32px;height:32px;background-color:lightgrey;border-radius:50%}.side-menu-wrapper{position:absolute;left:0;right:85vw;display:grid;background:lightgray}.side-menu-wrapper>div:nth-child(2){border-top:2px solid rgba(0,0,0,0.3);border-bottom:2px solid rgba(0,0,0,0.3)}.side-menu-wrapper .menu-item{padding:1rem 1.5rem;display:grid;grid-auto-flow:column;grid-column-gap:.5rem;-webkit-column-gap:.5rem;column-gap:.5rem;cursor:pointer;transition:background-color 0.3s linear;color:#5f6cb0}.side-menu-wrapper .menu-item:hover{background-color:rgba(95,108,176,0.2)}.side-menu-wrapper>.menu-footer{padding:1rem .5rem 3rem .5rem;display:grid;text-align:center}.dashboard-content{padding:1.5rem 1rem}.dashboard-content h1,.dashboard-content h3{color:white}.dashboard-content .welcome{display:grid;grid-area:1 / 1 / span 1 / span 2;grid-template-columns:1fr 1fr;border-radius:.3rem;background:linear-gradient(135deg, #3fcac5, #4265c7);padding:1rem;color:#fff}.dashboard-content .welcome p{text-align:justify;max-width:60ch}.dashboard-content .welcome>.values-holder{display:grid;grid-template-rows:1fr 60px}.dashboard-content .welcome>.values-holder>div{place-self:center}.dashboard-content .welcome>.values-holder img{width:250px;height:auto}.dashboard-content .welcome>.values-holder .values{display:grid;grid-auto-flow:column;grid-column-gap:.5rem;-webkit-column-gap:.5rem;column-gap:.5rem}.dashboard-content .welcome>.values-holder .value-item{display:flex;align-items:center;grid-gap:.5rem;gap:.5rem;height:60px;text-transform:uppercase}.dashboard-content .welcome>.values-holder .value-item>h1{color:#F0CF85}.dashboard-content>div:nth-child(1){display:grid;grid-template-columns:.4fr 1fr;grid-gap:1rem;gap:1rem}.ant-table-wrapper{height:100%}.quotes-list{overflow:hidden;max-height:347px}.quotes-list .ant-card-body{overflow:auto;height:287px}.quotes-list .actions{display:flex;flex-flow:wrap;grid-row-gap:5px;row-gap:5px;grid-column-gap:5px;-webkit-column-gap:5px;column-gap:5px}.edit-modal .ant-modal-body{display:grid;grid-template-columns:1fr 1fr;grid-gap:.5rem;gap:.5rem}.edit-modal .ant-modal-body .ant-select{overflow:hidden}.quick-quote .ant-select{overflow:hidden}.quick-quote .ant-card-body{display:grid;grid-template-columns:1fr 1fr;grid-gap:1rem;gap:1rem}.quick-quote .ant-card-body::before{position:absolute}.quick-quote .ant-card-body .people-box{display:grid;grid-template-columns:1fr 1fr;place-items:center end}.quick-quote .ant-card-body .people-box>label{place-self:center}

