.dashboard-content {
	padding: 1.5rem 1rem;
	& h1, h3 {
		color: white;
	}
	& .welcome {
		display: grid;
		grid-area: 1 / 1 / span 1 / span 2;
		grid-template-columns: 1fr 1fr;
		border-radius: .3rem;
		background: linear-gradient(
			135deg,
			#3fcac5,
			#4265c7
		);
		padding: 1rem;
		color: #fff;
		& p {
			text-align: justify;
			max-width: 60ch;
		}
		> .values-holder {
			display: grid;
			grid-template-rows: 1fr 60px;
			> div {
				place-self: center;
			}
			& img {
				width: 250px;
				height: auto;
			}
			& .values {
				display: grid;
				grid-auto-flow: column;
				column-gap: .5rem;
			}
			& .value-item {
				display: flex;
				align-items: center;
				gap: .5rem;
				height: 60px;
				text-transform: uppercase;
				> h1 {
					color: $light-orange;
				}
		}
		}
	}
	> div:nth-child(1) {
		display: grid;
		grid-template-columns: .4fr 1fr;
		gap: 1rem;
	}
}


.ant-table-wrapper {
	height: 100%;
}

.quotes-list {
	overflow: hidden;
	max-height: 347px;
	& .ant-card-body {
		overflow: auto;
		height: 287px;
	}
	& .actions {
		display: flex;
		flex-flow: wrap;
		row-gap: 5px;
		column-gap: 5px;
	}
}

.edit-modal {
	& .ant-modal-body {
		display: grid;
		grid-template-columns: 1fr 1fr;
		gap: .5rem;
		& .ant-select {
			overflow: hidden;
		}
	}
}