.quick-quote {
	& .ant-select {
		overflow: hidden;
	}
	& .ant-card-body {
		display: grid;
		grid-template-columns: 1fr 1fr;
		gap: 1rem;
		&::before {
			position: absolute;
		}
		& .people-box {
				display: grid;
				grid-template-columns: 1fr 1fr;
				place-items: center end;
				> label {
					place-self: center;
				}
		}
	}
}
