.header-wrapper {
	display: grid;
	grid-template-columns: 60px 1fr .8fr .4fr;
	height: 60px;
	background-color: $main-blue;
	color: white;
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	z-index: 3;
	> .logo {
		display: grid;
		align-content: center;
		> h4 {
			margin: 0;
			color: #fff;
		}
	}
	> .menu-toggler {
		font-size: 32px;
		display: grid;
		place-content: center;
	}
	>.search {
		display: grid;
		align-content: center;
		> div {
			display: grid;
			grid-template-columns: 32px 1fr;
			background-color: rgba($color: #fff, $alpha: .7);
			border-radius: .3rem;
			> *:not(input) {
				place-self: center;
			}
			> input {
				border: none;
				outline: none;
				background: transparent;
				height: 32px;
			}
		}
	}
	> .options {
		display: grid;
		align-content: center;
		grid-auto-flow: column;
		place-items: center;
		height: 32px;
		align-self: center;
		> .avatar {
			width: 32px;
			height: 32px;
			background-color: lightgrey;
			border-radius: 50%;
		}
	}
}